.slider {
	height: 400px;
	background: url('../img/slider_back.jpeg') 0 0/100% 100% no-repeat;

}
.swiper {
	width: 100%;
	height: 400px;
}
.swiper-slide {
	width: 300px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.swiper-slide img {
	border-radius: 50%;
}
.swiper-slide div {
	width: 500px;
	margin-left: 20px;
}
.swiper-slide div h3 {
	color: white;
	font-size: 40px;
	font-weight: bold;
}
.swiper-slide div p {
	color: white;
	font-size: 17px;
	margin-top: 20px;
	margin-bottom: 30px;
}
.swiper-slide div a {
	background-color: yellow;
	padding: 10px 30px 10px 30px;
	border-radius: 20px;
	font-weight: bold;
}
.swiper-button-next {
	color: yellow;
}
.swiper-slide img {
	width: 300px;
	height: 300px;
}
@media(max-width: 768.9px) {
.swiper-slide img {
	width: 200px;
	height: 200px;
}
.swiper-slide div {
	max-width: 400px;
	margin-left: 20px;
}
.swiper-slide div h3 {
	color: white;
	font-size: 35px;
	font-weight: bold;
}
.swiper-slide div p {
	color: white;
	font-size: 15px;
	margin-top: 20px;
	margin-bottom: 30px;
}
.swiper-slide div a {
	background-color: yellow;
	padding: 10px 30px 10px 30px;
	border-radius: 20px;
	font-weight: bold;
}
}
@media(max-width: 425.9px) {
	.slider {
		height: 400px;
	}
	.swiper-slide {
		display: grid;
		justify-content: center;
		text-align: center;
	}
	.swiper-slide div p {
	color: white;
	font-size: 12px;
	margin-top: 20px;
	margin-bottom: 0;
	padding-bottom: 40px;
	position: relative;
}
.swiper-slide div h3 {
	color: white;
	font-size: 30px;
	font-weight: bold;
}
}
@media(max-width: 320px) {
